import { createContext, useState } from 'react';

const btns = {
  red: {
    clicked: false,
  },
  yellow: {
    clicked: false,
    banned: false,
  },
  green: {
    clicked: false,
    banned: false,
  },
}

const state = {
  main: false, //     
  modal: false, //    
  music: {
    //  / /  ,
    index: 0,
    play: null,
  },
  stopMusic: false, //          
  iconRelated: {
    music: {
      appClosed: false,
      iconClicked: false,
    },
    sticker: {
      appClosed: false,
      iconClicked: false,
    },
  },
  browser: {
    width: '',
    height: '',
  },
  btn: {
    gif: {
      bookclub: {
        ...btns,
      },
      spotify: {
        ...btns,
      }
    },
    file: {
      bookclub: {
        ...btns,
      },
      spotify: {
        ...btns,
      },
      intro: {
        ...btns,
      }
    },
  },
  currentFolder: null,
  currentFile: {
    bookclub: null,
    spotify: null,
    intro: null,
  },
  currentList: {
    clicked: null,
    types: null,
    list: null,
  },
  currentType: {
    type: null,
    ways: null,
    hovered: false,
  },
};

const actions = {
  setModal: () => {},
  setMain: () => {},
  setMusic: () => {},
  setStopMusic: () => {},
  setIconRelated: () => {},
  setBrowser: () => {},
  setBtn: () => {},
  setCurrentFolder: () => {},
  setCurrentFile: () => {},
  setCurrentList: () => {},
  setCurrentType: () => {},
};

const Context = createContext({
  state,
  actions,
});

const Provider = ({ children }) => {
  const [main, setMain] = useState(false);
  const [modal, setModal] = useState(false);
  const [music, setMusic] = useState({
    index: 0,
    play: null,
  });
  const [stopMusic, setStopMusic] = useState(false);
  const [iconRelated, setIconRelated] = useState({
    music: {
      appClosed: false,
      iconClicked: false,
    },
    sticker: {
      appClosed: false,
      iconClicked: false,
    },
  });
  const [browser, setBrowser] = useState({
    width: '', //  
    height: '', // Center 
  });
  const [btn, setBtn] = useState({
    gif: {
      bookclub: {
        ...btns,
      },
      spotify: {
        ...btns,
      }
    },
    file: {
      bookclub: {
        ...btns,
      },
      spotify: {
        ...btns,
      },
      intro: {
        ...btns,
      }
    },
  });
  const [currentFolder, setCurrentFolder] = useState(null);
  const [currentFile, setCurrentFile] = useState({
    bookclub: null,
    spotify: null,
    intro: null,
  });
  const [currentList, setCurrentList] = useState({
    clicked: false,
    types: null,
    list: null,
  });
  const [currentType, setCurrentType] = useState({
    type: null,
    ways: null,
    hovered: false,
  });

  const value = {
    state: {
      modal,
      main,
      music,
      stopMusic,
      iconRelated,
      browser,
      btn,
      currentFolder,
      currentFile,
      currentList,
      currentType,
    },
    actions: {
      setModal,
      setMain,
      setMusic,
      setStopMusic,
      setIconRelated,
      setBrowser,
      setBtn,
      setCurrentFolder,
      setCurrentFile,
      setCurrentList,
      setCurrentType,
    },
  };
  return <Context.Provider value={value}>{children}</Context.Provider>;
};

export { Provider, Context };
