import React, { useContext } from 'react';
import Booting from './Components/Booting';
import Main from './Components/Main';
import { Context } from './Context/Context';
// main  false,      . 
// true     

function App() {
  const { state: { main } } = useContext(Context);
  return (
      <>
        { !main && <Booting/>}
        { main && <Main/>}
      </>
  );
}

export default App;
